import request from './request'

/*****************************************************/
//  
export function shelfList(params) {
    return request({
        url: 'storage/list',
        method: 'post',
        params: params
    })
}

//  
export function materialList(params) {
    return request({
        url: 'material/list',
        method: 'post',
        params: params
    })
}

//
export function shelfListPagin(params) {
    return request({
        url: 'shelf/list',
        method: 'post',
        params: params
    })
}

//
export function shelfUpdate(params) {
    return request({
        url: 'shelf/update',
        method: 'post',
        params: params
    })
}

//  
export function storageDetailList(params) {
    return request({
        url: 'storagedetail/list',
        method: 'post',
        params: params
    })
}

//  
export function storageDetailUpdate(params) {
    return request({
        url: 'storagedetail/update',
        method: 'post',
        params: params
    })
}



